@font-face {
    font-family: "Ginger";
    src: url("../fonts/Ginger.woff") format("woff"),
        url("../fonts/Ginger.ttf") format("truetype");
}
@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@300;400&display=swap");

$yellow: #fa9f41;
$light-yellow: #fdefe5;
$black: #000000;
$white: #ffffff;


body {
    font-family: "Heebo", sans-serif;
    font-weight: 300;
}
h1 {
    font-family: "Ginger", sans-serif;

    font-size: 80px;
    font-weight: 600;
    text-align: center;
}
h2 {
    font-family: "Ginger", sans-serif;

    font-size: 60px;
    font-weight: 600;
}
h3 {
    font-size: 50px;
}
h4 {
    font-size: 40px;
}
h5 {
    font-size: 30px;
}
h6 {
    font-size: 20px;
}
b{
    font-weight: 500;
}
p, input, textarea {
    font-family: "Heebo", sans-serif;
    font-weight: 300;
    font-size: 18px;
}
label {
    font-family: "Ginger", sans-serif;
font-weight: 800;
    font-size: 20px;
}

.portfolio {
    min-height: 100vh;
    overflow: hidden;
}

@media screen and (max-width: 768px) {
    h1{
        font-size: 60px;
    }
}

@media screen and (max-width: 425px) {
    h1{
        font-size: 45px;
    }
  h2{
    font-size: 40px;
  }
  h5{
    font-size: 25px;
  }
//   p{
//     font-size:15px;
//   }
}
